.markdownShower {
  text-align: left;
  vertical-align: top;
  box-sizing: border-box;
  width: 100%;
  display: inline-block;
  overflow-x: auto;
}

.markdownShower blockquote {
  border-left: 2px solid #ccc;
  margin: 0;
  padding: 2px 0 2px 10px;
  font-size: 1.05em;
}

.markdownShower img {
  max-width: 300px;
  max-height: 300px;
}

.markdownShower h1 {
  color: var(--color);
  margin-bottom: .3em;
  padding-bottom: .3em;
}

.markdownShower h2 {
  color: var(--color);
  margin-bottom: .3em;
  padding-bottom: .3em;
}

