.markdownShower ul {
  list-style-type: circle;
}

.markdownShower p {
  white-space: pre-wrap;
  margin-top: .3rem;
  margin-bottom: .3rem;
}

.markdownShower :global(.keyword) {
  color: #908080;
  box-sizing: border-box;
  background: #ff0;
  border-radius: 4px;
  margin: 0 3px;
  padding: 0 3px;
  font-size: 14px;
}

.markdownShower :global(code.hljs) {
  margin: 0;
}

code {
  color: #dcdcdc;
  background: #797979;
  border-radius: 6px;
  margin: 0 4px;
  padding: 3px 8px;
}

